<template>
    <NoteEditor v-if="showEditor" 
        v-model="currentNote"
        :is-new="newNote"
        @hide-editor="hideEditor" 
        @update="createOrUpdateNote"
        @delete="deleteNote" />
    <div v-if="!showEditor" class="container note-list">
        <p v-if="notes.length===0" class="fs-5 text-muted">I know I'm empty right now but I'm full of potential...</p>
        <ul class="list-group list-group-hover">
            <li v-for="(note) in sortedNotes" :key="note.id" @click="selectNote(note)" class="list-group-item" :class="{'li-active' : currentNote.id===note.id}">
                <div class="d-flex align-items-center">
                    <div class="fs-5 flex-fill">{{ note.title }}</div>
                    <div class="ms-2 badge text-bg-info">{{ new Date(note.id).toLocaleDateString(getUserLocale()) }}</div>
                </div>
                <div class="d-flex align-items-center">
                    <div class="text-truncate text-muted fs-6 flex-fill">{{ note.text }}</div>
                </div>
            </li>
          </ul>
    </div>
    <FixedBottom v-if="!showEditor">
        <button @click="startNewNote" style="height: 50px;width: 50px;" class="btn btn-primary rounded-circle fs-5 shadow" type="button">
            <i class="bi bi-plus-lg"></i>
        </button>
    </FixedBottom>
</template>

<script setup>
import { ref, onMounted, computed, watch } from 'vue';
import NoteEditor from './NoteEditor.vue';
import FixedBottom from './FixedBottom.vue';
const notes = ref([]);
const showEditor = ref(false);
const newNote = ref(false);
const currentNote = ref();

function hideEditor(){
    showEditor.value = false;
}

const loadNotes = () => {
  notes.value = JSON.parse(localStorage.getItem('notes')) || [];
  currentNote.value = {};
};


const sortedNotes = computed(() => {
  return [...notes.value].sort((a,b)=> b.id-a.id)
});

const selectNote = (note) => {
    currentNote.value = note;
    newNote.value = false;
    setTimeout(() => {
      showEditor.value = true;
    }, 100);
}

const saveNotes = () => {
    localStorage.setItem('notes', JSON.stringify(notes.value));
}

const createOrUpdateNote = (updatedNote) => {
  const index = notes.value.findIndex(note => note.id === updatedNote.id);
  if (index !== -1) {
    notes.value[index] = updatedNote;
  } else if (newNote.value) {
    notes.value.push(updatedNote);
  }
  saveNotes();
};

const deleteNote = (id) => {
  notes.value = notes.value.filter(note => note.id !== id);
  saveNotes();
};

function startNewNote(){
    currentNote.value = {id:Date.now(), title:"",text:""};
    showEditor.value = true;
    newNote.value = true;
}
watch(() => showEditor.value,
    (newValue) => {
        if(!newValue){
            currentNote.value = {};
        }
    }
)
onMounted(loadNotes);
</script>
<style scoped>
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}
.note-list {
    overflow-x: hidden;
    animation: fadeIn 500ms ease forwards;
}
.li-active, li:active {
    background-color: var(--bs-primary) !important;
    border-color: none !important;
    color: color-mix(in srgb, var(--bs-primary) 30%,white) !important;
    -webkit-transition: background-color 100ms linear;
    -ms-transition: background-color 100ms linear;
    transition: background-color 100ms linear;
}
</style>