<template>
    <div class="container">
        <TodoItem
            :todos="incompleteTodos"
            :setCurrentTodo="setCurrentTodo"
            :save-todos="saveTodos"
        />
        <button id="showIncompleteButton" ref="showIncompleteButton" 
            class="btn btn-outline-secondary mb-3 w-100 text-start"
            :class="{'mt-3':incompleteTodos.length>0}"
            @click="toggleShowCompleted($event)">
            <i :class="chevronClass"></i>
            Completed {{ completedCount }}
        </button>
        <Transition>
            <TodoItem
                v-show="showCompleted"
                :visible="showCompleted"
                :todos="completedTodos"
                :set-current-todo="setCurrentTodo"
                :save-todos="saveTodos"
            />
        </Transition>
    </div>
    <TodoModal
        v-if="currentTodo !== null"
        modalId="editTodoModal"
        :is-new="false"
        :todo="currentTodo"
        :delete-todo="deleteTodo"
        :save-todos="saveTodos"
    />
    <FixedBottom>
        <Transition>
            <input
                v-show="showInput"
                ref="todoInput"
                list="itemOptions"
                style="height: 45px;"
                type="text"
                class="form-control bg-secondary-subtle"
                placeholder="Add a new item..."
                v-model.trim="newTodo"
                @keyup.enter="addTodo"
                @focusout="showInput=false"
            />
        </Transition>
        <datalist id="itemOptions">
            <option v-for="(name, index) in uniqueItems" :key="index" :value="name" >
            </option>
        </datalist>
        <button v-show="!showInput" @click="showInput = !showInput" style="height: 50px;width: 50px;" class="btn btn-primary rounded-circle fs-5 shadow" type="button"><i class="bi bi-plus-lg"></i></button>
    </FixedBottom>
</template>

<script setup>
import TodoItem from './TodoItem.vue';
import TodoModal from './TodoModal.vue';
import FixedBottom from './FixedBottom.vue';
</script>

<script>

export default {
    data() {
        return {
            newTodo: '',
            todos: JSON.parse(localStorage.getItem('todoItems')) || [],
            showCompleted: false,
            currentTodo: {name:"",done:false,id:0},
            showInput:false
        };
    },
    computed: {
        sortedTodos() {
            return [...this.todos].sort((a,b)=>b.id - a.id);
        },
        completedTodos() {
          return this.sortedTodos.filter(todo => todo.done);
        },
        incompleteTodos() {
          return this.sortedTodos.filter(todo => !todo.done);
        },
        completedCount() {
          return this.completedTodos.length;
        },
        uniqueItems(){
            return [...new Set(this.todos.map(todo => todo.name))].sort()
        },
        chevronClass(){
            return this.showCompleted ? "bi bi-chevron-down" : "bi bi-chevron-right";
        }
      },
    methods: {
        capitalize(string) {
            return string.charAt(0).toUpperCase() + string.slice(1);
        },
        removeFocus(event) {
            setTimeout(()=>{
              event.target.style.outline = 'none';
              event.target.style.boxShadow = 'none';
              this.$refs.showIncompleteButton.blur();
            },250)
        },
        toggleShowCompleted(event){
            this.showCompleted = !this.showCompleted;
            this.removeFocus(event);
        },
        addTodo() {
            if (this.newTodo) {
                this.todos.push({ id: Date.now(), name: this.capitalize(this.newTodo), done: false });
                this.newTodo = '';
                this.saveTodos();
            }
        },
        setCurrentTodo(todo){
            this.currentTodo = todo;
        },
        deleteTodo(id) {
            this.todos = this.todos.filter(todo => todo.id !== id );
            this.saveTodos();
        },
        saveTodos() {
            localStorage.setItem('todoItems', JSON.stringify(this.todos));
        }
    },
    watch: {
        showInput(newValue){
            if (newValue) {
                this.$nextTick(()=>{
                    this.$refs.todoInput.focus();
                });
            }
        }
    }
};
</script>

<style scoped>
.btn {
  z-index: 1;
}
.v-enter-active {
    transition: opacity 0.5s ease;
}
.v-leave-active {
    transition: opacity 0.5s ease;
}
.v-enter-from {
    opacity: 0;
}
.v-leave-to {
    opacity: 0;
}
.container {
    overflow-x: hidden;
}
#showIncompleteButton {
    height: 2.5em;
}
#showIncompleteButton:active{
    background-color: color-mix(in srgb, var(--bs-secondary) 50%, black) !important;
    -webkit-transition: background-color 100ms linear;
    -ms-transition: background-color 100ms linear;
    transition: background-color 100ms linear;
}
</style>
