<template>
    <div
        id="textInput"
        ref="textInput"
        :contenteditable="!props.disabled"
        class="form-control p-2"
        :class="{'temp text-muted': placeholderActive}"
        :innerText="displayText"
        spellcheck="false"
        @input="onInput"
        @focus="hidePlaceholder=true"
        @focusout="hidePlaceholder=false"
        @keydown.enter="onKeyDownEnter"
        @mouseup="positionToolbar"
        @contextmenu="onContextmenu"
        @keyup="positionToolbar"
        @touchstart="startPress"
        @touchend="endPress"
        @touchcancel="endPress"

        >
    </div>
    <div id="toolbar" class="d-flex flex-nowrap" :class="{'hidden':!showToolbar}">
        <button @click="mdFmt('h1')" class="btn btn-secondary shadow" type="button">
            <i class="bi bi-type-h1"></i>
        </button>
        <button @click="mdFmt('h2')" class="ms-1 btn btn-secondary shadow" type="button">
            <i class="bi bi-type-h2"></i>
        </button>
        <button @click="mdFmt('h3')" class="ms-1 btn btn-secondary shadow" type="button">
            <i class="bi bi-type-h3"></i>
        </button>
        <button @click="mdFmt('bold')" class="ms-1 btn btn-secondary shadow" type="button">
            <i class="bi bi-type-bold"></i>
        </button>
        <button @click="mdFmt('italic')" class="ms-1 btn btn-secondary shadow" type="button">
            <i class="bi bi-type-italic"></i>
        </button>
        <button @click="mdFmt('quote')" class="ms-1 btn btn-secondary shadow" type="button">
            <i class="bi bi-quote"></i>
        </button>
    </div>
</template>
<script setup>
import { ref, watch, defineModel, defineEmits, defineProps, computed, useTemplateRef } from 'vue'
const model = defineModel();
const localText = ref(model.value);
const hidePlaceholder = ref(false);
const textInput = useTemplateRef("textInput");
const showToolbar = ref(false);
const longPressDuration = ref(500); // ms
const emit = defineEmits(['input','keydown.enter']);
let pressTimer;
let currentSelection;

const props = defineProps({
    disabled:{type:Boolean},
    placeholder:{type:String},
    editorVisible:{type:Boolean},
})

const placeholderActive = computed(()=>{
    return displayText.value === props.placeholder && !hidePlaceholder.value;
})

const displayText = computed(()=>{
    if (model.value.trim() ==="" && !hidePlaceholder.value){
        return props.placeholder;
    } else {
        return localText.value;
    }
})

// Function to save the current selection range
const saveSelection = () => {
    const selection = window.getSelection();
    if (selection.rangeCount > 0) {
        const range = selection.getRangeAt(0);
        return {
            selection: selection,
            startContainer: range.startContainer,
            startOffset: range.startOffset,
            endContainer: range.endContainer,
            endOffset: range.endOffset,
            isCollapsed: range.collapsed //cursor only
        };
    }
    return null;
}

// Function to restore the selection range
const restoreSelection = (savedRange) => {
    if (savedRange) {
        const selection = window.getSelection();
        const range = document.createRange();
        
        // Set the start and end of the range
        range.setStart(savedRange.startContainer, savedRange.startOffset);
        range.setEnd(savedRange.endContainer, savedRange.endOffset);
        
        // Clear any existing selections and add the restored range
        selection.removeAllRanges();
        selection.addRange(range);
        
        // If the selection was collapsed (cursor only), ensure the cursor is at the original position
        if (savedRange.isCollapsed) {
            selection.collapse(savedRange.startContainer, savedRange.startOffset);
        }
    }
}

const getSelectionAtCursor = () => {
    const selection = currentSelection.selection;
    const textNode = selection.anchorNode;
    
    if (textNode && textNode.nodeType > 1) {
        const text = textNode.textContent;
        const selectedWords = selection.toString().split(" ");
        
        const lineRange = document.createRange();
        lineRange.setStart(textNode,0);
        lineRange.setEnd(textNode,text.length);

        if (selectedWords.length===1) {
            selection.modify("move","left","word");
            selection.modify("extend","right","word");
        }
        const wordRange = selection.getRangeAt(0);
        const word = selection.toString();
        restoreSelection(currentSelection);

        return {
            lineText: text,
            lineRange: lineRange,
            wordText: word,
            wordRange: wordRange
        }
    } else {
        return null;
    }
}

const positionToolbar = () => {
    showToolbar.value = true;
    const toolbar = document.getElementById('toolbar');
    currentSelection = saveSelection();
    const data = getSelectionAtCursor();
    if (data) {
        const rect = data.lineRange.getBoundingClientRect();
        //const toolbarWidth = document.getElementById("toolbar").offsetWidth;
        //const viewportWidth = window.innerWidth
        //const maxLeft = viewportWidth-toolbarWidth;
        // Position the toolbar above the selected text
        toolbar.style.top = `${rect.top + window.scrollY - toolbar.offsetHeight}px`;
        toolbar.style.left = `${rect.left}px`;
    }

}

const fmtOptions = [
    {key:"bold", context:"word", prefix:"**", suffix:"**"},
    {key:"italic", context:"word", prefix:"*", suffix:"*"},
    {key:"quote", context:"line", prefix:"> ", suffix:""},
    {key:"h1", context:"line", prefix:"# ", suffix:""},
    {key:"h2", context:"line", prefix:"## ", suffix:""},
    {key:"h3", context:"line", prefix:"### ", suffix:""}
]

const mdFmt = (fmt) => {
    currentSelection = saveSelection();
    const data = getSelectionAtCursor(currentSelection.selection);
    if (data) {
        document.execCommand("cut");
        const selectRange = (rng) => {currentSelection.selection.removeAllRanges();currentSelection.selection.addRange(rng);}
        const insertText = (t) => {document.execCommand("insertText",false,t)};
        const fmtOpt = fmtOptions.find(o => o.key===fmt);

        if (fmtOpt.context==="word") {
            data.wordText = data.wordText.replace(/\*/g,"").trim();
            let textBefore = data.lineText.slice(0,data.wordRange.startOffset);
            let textAfter = data.lineText.slice(data.wordRange.endOffset);
            while (textBefore.slice(-1)==="*") {
                textBefore = textBefore.slice(0,textBefore.length-1);
            }
            selectRange(data.lineRange);
            insertText(textBefore + fmtOpt.prefix + data.wordText + fmtOpt.suffix + textAfter);
        } else {
            data.lineText = data.lineText.replace(/>|#/g,"").trim();
            selectRange(data.lineRange);
            insertText(fmtOpt.prefix + data.lineText + fmtOpt.suffix);
        }
}
}

const startPress = () => {
    pressTimer = setTimeout(() => {
        const selection = window.getSelection();
        const data = getSelectionAtCursor(selection);
        selection.removeAllRanges();
        selection.addRange(data.wordRange);
        navigator.vibrate(50);
    }, longPressDuration);
}

const endPress = () => {
    clearTimeout(pressTimer);
}

const onContextmenu = (event) => {
    event.preventDefault();
    showToolbar.value = true;
}

const nlPattern = /^(\d+)\..+$/;
const ulPattern = /^- .+$/;

const onKeyDownEnter = (event) => {
    event.preventDefault();
    const textNode = window.getSelection().anchorNode;
    document.execCommand("insertText",false,"\n");

    if (textNode){
        const nlMatch = nlPattern.exec(textNode.textContent);
        const ulMatch = ulPattern.exec(textNode.textContent);
        if (nlMatch) {
            let nextN = parseInt(nlMatch.at(0))+1;
            document.execCommand("insertText",false,`${nextN}. `);
        } else if (ulMatch) {
            document.execCommand("insertText",false,"- ");
        }
    }
    emit('keydown.enter');
}

const onInput = (event) => {
    model.value = event.target.innerText;
    emit('input');
}

// Focus the div when the editor becomes visible
watch(
    () => props.editorVisible,
    (newValue) => {
        if(newValue && textInput){
            setTimeout(() => {
                // All of this is to put the cursor at the end of the content
                const range = document.createRange();
                const selection = window.getSelection();
                range.selectNodeContents(textInput.value);
                range.collapse(false); // <-- Set the cursor at the end of the selection
                selection?.removeAllRanges();
                selection?.addRange(range);
                // And this is just the focus part
                textInput.value.focus();
            }, 0);
        }
    },{immediate:true}
)

</script>
<style scoped>
div {
    min-height: 2.5em;
    white-space: pre-wrap !important;
    overflow-wrap: normal !important;
}
.temp {
    user-select: none;
}
#toolbar {
    position: absolute;
    padding: 5px;
    z-index: 1000;
    overflow-x: scroll;
}
#textInput {
    margin-bottom: 4em;
}
.hidden {
    display: none !important;
}
/*** Works on common browsers ***/
::selection {
    background-color: var(--bs-info);
    color: #fff;
}

/*** Mozilla based browsers ***/
::-moz-selection {
    background-color: var(--bs-info);
    color: #fff;
}

/***For Other Browsers ***/
::-o-selection {
    background-color: var(--bs-info);
    color: #fff;
}

::-ms-selection {
    background-color: var(--bs-info);
    color: #fff;
}

/*** For Webkit ***/
::-webkit-selection {
    background-color: var(--bs-info);
    color: #fff;
}

</style>